* {
  box-sizing: border-box;
}
table {
  color: #f5f4e8;
  border-collapse: collapse;
  margin: 0 auto;
  width: 400px;
  box-shadow: 3px 3px 10px black;
}
thead {
  background-color: #f45f00;
}
tbody {
  background-color: #13334c;
}
th, td {
  width: 50%;
  padding-left: 36px;
  text-align: left;
  line-height: 50px;
}
form {
  width: 400px;
  margin: 0 auto;
  margin-top: 16px;
  box-shadow: 3px 3px 10px;
  display: flex;
}
input {
  border: none;
  padding: 5px;
}
input:focus {
  outline: none;
}
input[type="text"], input[type="number"] {
  width: 40%;
}
input[type="submit"] {
  background-color: #f45f00;
  width: 20%;
}
button {
  display: none;
  background-color: red;
  padding: 4px;
  position:relative;
  left: 80px;
  border: none;
  border-radius: 2px;
}
tr:hover {
  border: 2px solid red;
}
tr:hover button {
  display: inline-block;
}
@media (max-width: 400px) {
  table, form {
    width: 100%;
  }
}